import React, {Component} from "react";
import { ReactComponent as ExpandMenuIco } from "../../assets/icons/expand-menu.svg";
import {ReactComponent as CloseMenuIco} from "../../assets/icons/close-outline.svg"
import styled from "styled-components";
import {Router, Link, BrowserRouter} from "react-router-dom";
import CompassLogo from "../../assets/icons/Logo.png"

const Container = styled.div`
  width: 80%;
  margin: 30px auto auto;
  height: 70px;
  text-align: right;
  
`

const Logo = styled.div`
  float: left;
  color: rgb(31, 45, 61);
  font-size: 25px;
  line-height: 30px;
  @media(max-width: 900px) {
  }
`

const LogoText = styled.div`
  float: right;
`

const CompassWrapper = styled.div`
  display: inline-block;
  float: left;
  margin-right: 5px;
`

const NavButton = styled.div`
  //float: right;
  display: inline;
  font-size: 20px;
  
  margin-left: 1%;
  margin-right: 1%;
  color: rgba(31, 45, 61, 0.5);
  transition: all 0.5s ease 0s;
  &:hover {
    cursor: pointer;
    color: rgb(31, 45, 61);
  }
  @media(max-width: 900px) {
    position: relative;
    display: ${props => props.show ? 'block' : 'none'};
    width: 100%;
    margin-bottom: 7px;
    float: right;
  }
`;

const MenuDiv = styled.div`
  float: right;
  width: 30px;
  display: none;
  &:hover {
    cursor: pointer;
  }
  @media(max-width: 900px) {
    display: block;
  }
`;

const Spacer = styled.div`
  display: none;
  position: relative;
  float: right;
  width: 100%;
  height: 30px;
  @media(max-width: 900px) {
    display: ${props => props.show ? 'block' : 'none'};
  }
`

const StyledLink = styled(Link)`
  font-size: 20px;
  color: rgba(31, 45, 61, 0.5);
  transition: all 0.5s ease 0s;
  text-decoration: none;
   &:hover {
    cursor: pointer;
    color: rgb(31, 45, 61);
  }
`;

class NavBar extends Component {
  state = {
      showApplyOptions: false
  }
  render() {
      return (
    <Container>
        <Logo>
            <CompassWrapper>
                <img src={CompassLogo} width="30px"/>
            </CompassWrapper>
            <LogoText>Compass Mentors</LogoText>
        </Logo>
        <MenuDiv onClick={this.props.clicked.toggleNav}>
            {this.props.displayNav ? <CloseMenuIco/> : <ExpandMenuIco/>}
        </MenuDiv>
        <NavButton show={this.props.displayNav} onClick={this.props.clicked.disableNav}>
            <StyledLink to="/">Home</StyledLink>
        </NavButton>
        <NavButton show={this.props.displayNav} onClick={this.props.clicked.disableNav}>
            <StyledLink to="/team">Team</StyledLink>
        </NavButton>
        <NavButton show={this.props.displayNav} onClick={this.props.clicked.disableNav}>
            <StyledLink to="/apply-now">Apply Now</StyledLink>
        </NavButton>
        <NavButton show={this.props.displayNav} onClick={this.props.clicked.disableNav}>
            <StyledLink to="/FAQ">FAQ</StyledLink>
        </NavButton>
        <NavButton show={this.props.displayNav} onClick={this.props.clicked.disableNav}>
            <StyledLink to="/code-of-conduct">Code of Conduct</StyledLink>
        </NavButton>
        <Spacer show={this.props.displayNav}/>

    </Container>
  );
  }


}

export default NavBar;