import React, {Component} from "react";
import styled from "styled-components";
import { ReactComponent as ArrowIcon } from "../../../assets/icons/arrow-ios-forward-outline.svg";

const OuterWrapper = styled.div`
  width: 80%;
  overflow: auto;
  margin: auto;
  text-align: left;
`

const Header = styled.h1`
  color: #FC8102;
  font-weight: bold;
`



const Container = styled.div`
  overflow: auto;
`;

const Wrapper = styled.div`
  width: 100%;
  color: rgb(31, 45, 61);
  overflow: auto;
  text-align: left;
  margin-bottom: 10px; 
`

const Form = styled.iframe`
  position: relative;
  @media(max-width: 600px) {
    height: 3000px;
  }
`;

const Arrow = styled.div`
  display: inline-block;
  width: 25px;
  float: left;
  transition: all 0.2s ease 0s;
  transform: ${props => props.show ? "rotate(90deg)" : "rotate(0deg)"};
  margin-right: ${props => props.show ? 5 : 0}px;
  &:hover {
    cursor: pointer;
    color: rgb(31, 45, 61, 0.5);
    fill: rgb(31, 45, 61, 0.5);
  }
`

const Question = styled.div`
  width: auto;
  line-height: 25px;
  font-size: 20px;
  transition: all 0.2s ease 0s;
  margin-left: 5px;
  &:hover {
    cursor: pointer;
    color: rgb(31, 45, 61, 0.5);
    fill: rgb(31, 45, 61, 0.5);
  }
`;

const AnswerContainer = styled.div`
  margin-left: 30px;
  display: ${props => props.show ? 'block' : 'none'};
  margin-top: 10px;
  margin-bottom: 10px;
  line-height: 20px;
`

class ApplyNow extends Component {
  state = {
    showMentor: false,
    showStudent: false
  }

  StudentClickedHandler = () => {
    this.setState({showStudent: !this.state.showStudent});
  }

  MentorClickedHandler = () => {
    this.setState({showMentor: !this.state.showMentor});
  }

  render() {
    return (
      <OuterWrapper>
        <Header>Applications</Header>
        <Wrapper onClick={this.StudentClickedHandler}>
          <Arrow show={this.state.showStudent}><ArrowIcon/></Arrow>
          <Question>Students</Question>
          <AnswerContainer show={this.state.showStudent}>
            <Container>
              <Form
                src="https://docs.google.com/forms/d/e/1FAIpQLSfYamoc_pyO53z17-62aK0YheYm_tZTH9fIfIB76W24_4Z1Pg/viewform?embedded=true"
                width="80%" height="2200" frameBorder="0" marginHeight="0" marginWidth="0">Loading…
              </Form>
            </Container>
          </AnswerContainer>
        </Wrapper>
        <Wrapper onClick={this.MentorClickedHandler}>
          <Arrow show={this.state.showMentor}><ArrowIcon/></Arrow>
          <Question>Mentor</Question>
          <AnswerContainer show={this.state.showMentor}>
            <Container>
              <Form
                src="https://docs.google.com/forms/d/e/1FAIpQLSdRrckXf6HHa7q_RFVjl88LHaE_iCopGYiCLZ4rvijw3MTJXw/viewform?embedded=true"
                width="80%" height="2200" frameBorder="0" marginHeight="0" marginWidth="0">Loading…
              </Form>
            </Container>
          </AnswerContainer>
        </Wrapper>
      </OuterWrapper>


    )
  }


}

export default ApplyNow;